.Congrats {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    
    
    .column1 {
        background: white;
        height: 45%;
        width: 100%;
        overflow: hidden;

        .congrats_box {
            width: 100%;
            display: flex;
            flex-direction: column;
        }

        .link_container::before {
            position: relative;
            display: inline-block;
            content: "";
            top: -4px;
            left: -10px;
            background: #0D1D41;
            width: 30%;
            height: 1px;
        }

        .link_container {
            font-family: 'Lota Grotesque Alt 1';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 26px;
            text-align: center;
            letter-spacing: -0.03em;
            color: #0D1D41;
        }

        .link_container::after {
                position: relative;
                display: inline-block;
                content: "";
                top: -4px;
                left: 18px;
                background: #0D1D41;
                width: 30%;
                height: 1px;
        }

        .text {
            font-family: 'Lota Grotesque Alt 1';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 26px;
            text-align: center;
            letter-spacing: -0.03em;
            color: #0D1D41;
            padding: 0px 70px 0 70px;
        }
    }

    .column2 {
        height: 55%;
        width: 100%;

        .img_box {
            margin: 0px 40px 20px 40px;
            background: url("./../img/maison.png");
            // background-color: aqua;
            height: 230px;
            position: relative;
            top: -55px;
            border: 1px solid #FFFFFF;
            background-position: center;

        }

        .content_mint {
            margin: 20px 40px 20px 40px;
            position: relative;
            top: -70px;
            height: 100px;
            padding: 20px 30px 20px 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;

            a {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 21px;

                text-align: center;
                letter-spacing: -0.03em;
                text-transform: uppercase;

                color: #0D1D41;
                padding: 15px 1px 15px 1px;
                background-color: #69ACDF;
                border: 1px solid #FFFFFF;
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
            }
        }

        .faq_div {
            position: relative;

            span::before {
                position: relative;
                display: inline-block;
                content: "";
                // top: 33px;
                left: 30px;
                top: 20px;
                background: url('./../img/case.png');
                background-size: contain;
                width: 81px;
                height: 81px;
            }
            span {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 21px;
                width: 100%;
                /* identical to box height */
                
                text-align: end;
                letter-spacing: -0.03em;
                
                color: #FFFFFF;
                // width: 100%;
                display: inline-block;
                // margin-left: 50px;
                margin-bottom: 80px;
            }
                .box_carre {
                position: relative;
                display: inline-block;
                content: "";
                top: -4px;
                left: 20px;
                background: white;
                background-size: contain;
                width: 30%;
                height: 2px;
            }
        }
    }

    .box_image {
        text-align: center;
        align-items: center;
        display: flex;
        height: 150px;
        justify-content: center;
        background: transparent;
    }


    .box_balloon {
        width: 100%;
        height: 0px;

        img {
            width: 100%;
        }
    }
    .box {
        width: 80%;
        height: 100%;
        border: 1px solid #0D1D41;
        margin: auto;
        border-bottom: 0px;
    }
    .div_balloon {
        width: 100%;
        height: 40%;

    }
}


@media only screen and (max-width: 420px) {
    .Congrats {
        .column2 {
            .faq_div {

                .box_carre {
                    position: relative;
                    display: inline-block;
                    content: "";
                    top: -4px;
                    left: 20px;
                    background: white;
                    background-size: contain;
                    // width: 100px;
                    // width: 40%;
                    width: 25%;
                    height: 2px;
                }
            }
        }
    }
}


@media only screen and (max-width: 345px) {
    .Congrats {
        .column2 {
            .faq_div {
                span {
                    text-align: center;
                }
                .box_carre {
                    position: relative;
                    display: inline-block;
                    content: "";
                    top: -4px;
                    left: 20px;
                    background: white;
                    background-size: contain;
                    width: 15%;
                    height: 2px;
                }
            }
        }
    }
}