
.Mint_page {
    height: 100%;
    width: 100%;
    overflow-x: hidden;
    
    
    .column1 {
        background: white;
        height: 70%;
        width: 100%;
        overflow: hidden;

    }

    .column2 {
        height: 30%;
        width: 100%;

        .content_mint {
            margin: 20px 40px 20px 40px;
            position: relative;
            top: -100px;
            height: 180px;
            mix-blend-mode: normal;
            background: #FFFFFF;
            box-shadow: inset 0px 0px 5px rgba(0, 0, 0, 0.2);
            padding: 20px 30px 20px 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;

            a {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 21px;

                text-align: center;
                letter-spacing: -0.03em;
                text-transform: uppercase;

                color: #0D1D41;
                padding: 15px 10px 15px 10px;
                background-color: #69ACDF;
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
            }

            .button_stats {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 25px;
                /* identical to box height, or 139% */

                text-align: center;
                letter-spacing: -0.03em;
                margin-top: 4vh;

                color: #0D1D41;
            }

            .stats {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 25px;
                /* identical to box height, or 139% */

                text-align: center;
                letter-spacing: -0.03em;

                color: #0D1D41;
            }
        }

        .box_title  {
            display: flex;
            flex-direction: column;
            position: relative;
            top: -80px;

            .title {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 400;
                font-size: 22px;
                line-height: 29px;
                letter-spacing: -0.03em;
                width: 100%;
                color: #FFFFFF;
                display: inline-block;
                margin-left: 50px;
                margin-bottom: 10px;
            }
    
            h2::after {
            position: relative;
            display: inline-block;
            content: "";
            top: -5px;
            left: 15px;
            background-color: white;
            width: 60%;
            height: 2px;
            }

            .left {
                position: relative;
                top: 3px;

                span {
                    font-family: 'Lota Grotesque Alt 1';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 25px;
                    letter-spacing: -0.03em;
                    color: #0D1D41;
                    background-color: white;
                    padding: 5px 10px 5px 10px;
                    margin: 0px 15px 0px 0px;
                }
            }

            .right {
                h3 {
                    font-family: 'Lota Grotesque Alt 1';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 18px;
                    letter-spacing: -0.03em;
                    margin-bottom: 5px;
                    color: #FFFFFF;
                }

                span {
                    font-family: 'Lota Grotesque Alt 1';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 18px;
                    letter-spacing: -0.03em;
                    color: #FFFFFF;
                }
            }

            .box1 {
                display: flex;
                flex-direction: row;
                margin: 20px 60px 20px 50px; 
            }
            .box2 {
                display: flex;
                flex-direction: row;
                margin: 20px 60px 20px 50px; 
            }
            .box3 {
                display: flex;
                flex-direction: row;
                margin: 20px 60px 0px 50px; 
            }
        }

        .faq_div {
            position: relative;
            top: -40px;

            span::before {
                position: relative;
                display: inline-block;
                content: "";
                // top: 33px;
                left: 30px;
                top: 20px;
                background: url('./../img/case.png');
                background-size: contain;
                width: 81px;
                height: 81px;
            }
            span {
                font-family: 'Lota Grotesque Alt 1';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 21px;
                width: 100%;
                /* identical to box height */
                
                text-align: end;
                letter-spacing: -0.03em;
                
                color: #FFFFFF;
                // width: 100%;
                display: inline-block;
                // margin-left: 50px;
                margin-bottom: 80px;
            }

            // span::after {
                .box_carre {
                position: relative;
                display: inline-block;
                content: "";
                top: -4px;
                left: 20px;
                background: white;
                background-size: contain;
                width: 30%;
                height: 2px;
            }
        }
    }

    .box_image {
        text-align: center;
        align-items: center;
        display: flex;
        height: 170px;
        justify-content: center;
        background: transparent;
    }


    .box_balloon {
        width: 100%;
        height: 0px;

        img {
            width: 100%;
        }
    }
    .box {
        width: 80%;
        height: 100%;
        border: 1px solid #0D1D41;
        margin: auto;
        border-bottom: 0px;
    }
    .div_balloon {
        width: 100%;
        height: 40%;

    }
}


@media only screen and (max-width: 420px) {
    .Mint_page {
        .column2 {
            .faq_div {

                .box_carre {
                    position: relative;
                    display: inline-block;
                    content: "";
                    top: -4px;
                    left: 20px;
                    background: white;
                    background-size: contain;
                    // width: 100px;
                    // width: 40%;
                    width: 25%;
                    height: 2px;
                }
            }
        }
    }
}


@media only screen and (max-width: 345px) {
    .Mint_page {
        .column2 {
            .faq_div {
                span {
                    text-align: center;
                }
                .box_carre {
                    position: relative;
                    display: inline-block;
                    content: "";
                    top: -4px;
                    left: 20px;
                    background: white;
                    background-size: contain;
                    width: 15%;
                    height: 2px;
                }
            }
        }
    }
}